import rome from "rome";

import formats from "./formats";

// 080989200418
const datepicker = (() => {
    window.moment = window.moment || rome.moment;

    const _createDatepicker = datepicker => {
        const {
            datepickerFormat,
            datepickerTime,
            datepickerMin,
            datepickerMax,
            datepickerValue,
            datepickerMonths,
            required
        } = datepicker.dataset;
        const { dateFormat, hourFormat, locale } = formats[datepickerFormat] || formats.iso8601;

        if (datepickerFormat && !formats[datepickerFormat]) {
            console.warn(`${datepickerFormat} is not a recognised date format, using default date format instead.`);
        }

        rome.moment.updateLocale(datepickerFormat || "iso8601", locale);

        const options = {
            weekStart: 1,
            inputFormat: `${datepickerTime ? `${hourFormat} ` : ""}${dateFormat}`,
            time: !!datepickerTime || false,
            min: datepickerMin || null,
            max: datepickerMax || null,
            initialValue: datepickerValue || null,
            required: !!required || false,
            monthsInCalendar: parseInt(datepickerMonths) || 1
        };

        const datetable = rome(datepicker, options);

        // This is for getting around an issue with rome not displaying the datepicker when clicking label.
        // https://github.com/bevacqua/rome/issues/102
        if (datepicker.id) {
            const label = document.querySelector(`label[for=${datepicker.id}]`);

            label.addEventListener("click", e => {
                e.preventDefault();
                setTimeout(() => datepicker.focus(), 10);
            });
        }

        const _validDate = () => {
            const currentYear = rome.moment().format("YYYY");
            const currentMonth = rome.moment().format("MM");
            const dateTableYear = datetable.getDateString() ? datetable.getDateString("YYYY") : currentYear;
            const dateTableMonth = datetable.getDateString() ? datetable.getDateString("MM") : currentMonth;
            let daysInMonth;

            if (dateTableYear === currentYear) {
                if (dateTableMonth === currentMonth) {
                    daysInMonth = datetable.container.querySelectorAll(".rd-day-body:not(.rd-day-prev-month):not(.rd-day-next-month");
                    _setCurrentDay(daysInMonth);
                } else if (parseInt(dateTableMonth) === currentMonth + 1) {
                    daysInMonth = datetable.container.querySelectorAll(".rd-day-next-month");
                    _setCurrentDay(daysInMonth);
                } else if (parseInt(dateTableMonth) === currentMonth - 1) {
                    daysInMonth = datetable.container.querySelectorAll(".rd-day-prev-month");
                    _setCurrentDay(daysInMonth);
                }
            } else if (parseInt(dateTableYear) === parseInt(currentYear) + 1 && parseInt(dateTableMonth) === 1 && parseInt(currentMonth) === 12) {
                daysInMonth = datetable.container.querySelectorAll(".rd-day-prev-month");
                _setCurrentDay(daysInMonth);
            } else if (parseInt(dateTableYear) === parseInt(currentYear) - 1 && parseInt(dateTableMonth) === 12 && parseInt(currentMonth) === 1) {
                daysInMonth = datetable.container.querySelectorAll(".rd-day-next-month");
                _setCurrentDay(daysInMonth);
            }
        };

        const _setCurrentDay = daysInMonth => {
            const currentDay = rome.moment().format("Do");
            const datetableDay = [...daysInMonth].find(day => parseInt(day.innerHTML) === parseInt(currentDay));

            datetableDay ? datetableDay.classList.add("rd-current-day") : null;
        };

        datetable.on("show", () => _validDate());
        datetable.on("back", () => _validDate());
        datetable.on("next", () => _validDate());
    };

    const init = () => {
        const datepickers = document.querySelectorAll("[data-datepicker]");

        if (datepickers.length > 0) {
            datepickers.forEach(picker => {
                _createDatepicker(picker);
            });
        }
    };

    return { init };
})();

export default datepicker;
